    excerpt:
      '<b>Huawei</b>, Οθόνη: 6.26", Android 8.1, RAM: 3GB, Μνήμη: 32GB, Κάμερα: 13MP + 2MP, Selfie: 16MP, CPU: Οκταπύρηνος',
    description:
      '<p>Οθόνη 6,26 ιντσών, οκταπύρηνος επεξεργαστής, ισχυρή GPU, διπλή κάμερα με αισθητήρα βάθους, GPS, ραδιόφωνο... Η <strong><a href="https://www.bestprice.gr/b/2139/huawei.html" title="Huawei">Huawei</a></strong> μάς δίνει ένα ακόμα <strong><a href="https://www.bestprice.gr/cat/806/mobile-phones/f/1_2139/huawei.html" title="Κινητά Huawei">κινητό</a></strong> τηλέφωνο με εξαιρετικά χαρακτηριστικά και ασυναγώνιστη τιμή.</p>\n<p>Ο κομψός και απλός σχεδιασμός του <strong>Huawei Y7</strong> τού προσδίδει την εμφάνιση ενός κορυφαίου τηλεφώνου: πρόσθια επιφάνεια από γυαλί, οθόνη edge to edge, και μια κυρτή γυαλιστερή κεραμική πίσω όψη με απαλή αφή που ξεχωρίζει με τη διπλή κάμερα και τον αισθητήρα δακτυλικού αποτυπώματος. Η οθόνη καλύπτει το μεγαλύτερο μέρος της πρόσοψης εκτός από ένα μικρό περιθώριο στο κάτω μέρος και την οπή της selfie κάμερας στο πάνω. Τα διαθέσιμα χρώματα είναι Aurora Blue, Coral Red και Midnight Black.</p>\n<p>Η Dewdrop οθόνη είναι το πιο premium χαρακτηριστικό του Υ7 λόγω μεγέθους και θέσης. Μιλάμε για ένα «γενναιόδωρο» IPS LCD πάνελ 6,26 ιντσών με κάλυψη σε pixel που φτάνει το 80% της πρόσοψης. Η ανάλυσή της είναι 720 x 1520 pixels (269 ppi) και η αναλογία των διαστάσεων η απόλυτα κινηματογραφική 19:9. H μεγαλύτερη καθαρότητα και τα φυσικά χρώματα από κάθε γωνία θέασης δημιουργούν έναν πολύχρωμο κόσμο στο χέρι σου.</p>\n<p>Η καρδιά του Huawei Y7 είναι ο οκταπύρηνος Snapdragon 450 (14 nm). Ο ταχύτερος πυρήνας είναι χρονισμένος στα 1,8 GHz κάτι που, σε συνδυασμό με τα Android 8,1, υπόσχεται απρόσκοπτη και ομαλή λειτουργία. Με 3 GB RAM + 32 GB ROM δίνει τη δυνατότητα να χρησιμοποιούνται αβίαστα πολλές εφαρμογές συγχρόνως. Ο συνδυασμός και των 8 πυρήνων δίνει πολύ καλά αποτελέσματα και το πακέτο των επεξεργαστών ολοκληρώνει μια ισχυρή GPU Adreno 506.</p>\n<p>Στο Huawei Y7 έχουμε μια βασική πίσω κάμερα με αισθητήρα 13 ΜΡ, Phase Detection Auto Focus για ευκρινείς εικόνες και ανάλυση βίντεο στα 1080 p @ 30 fps. Το set up συμπληρώνεται από μία κάμερα 2 ΜΡ που λειτουργεί ως αισθητήρας βάθους για τέλεια πορτρέτα με bokeh εφέ.</p>\n<p>Σε ό,τι αφορά τη selfie κάμερα, έχουμε έναν πολύ ικανό αισθητήρα 8 MP, με υποστήριξη HDR, για πολύ καλές λήψεις σε όλες τις συνθήκες. Η αυτόματη επεξεργασία σού δίνει τέλειες φωτογραφίες και αναδεικνύει τον πιο όμορφο εαυτό σου. Όσο για τα βίντεο, η ανάλυση είναι και εδώ στα 1080 p @ 30 fps.</p>\n<p>Το Huawei Y7 είναι εφοδιασμένο με μία εντυπωσιακή ισχυρή μπαταρία 4000 mAh και υποστηρίζει ταχεία φόρτιση 10 Watt. Σέρφαρε στο διαδίκτυο, παίξε τα αγαπημένα σου παιχνίδια και τρέξε τις εφαρμογές που χρησιμοποιείς καθημερινά χωρίς να σκέφτεσαι την αυτονομία του κινητού σου. Ταξίδεψε ξέγνοιαστα, απόλαυσε την κάθε μέρα και ξέχνα το φορτιστή.</p>\n<p>Με δεδομένη την τιμή αγοράς, το Huawei Y7 είναι μια εξαιρετικά πολύ ελκυστική πρόταση. Σου παρέχει μια εξαιρετική οθόνη και μια αντίστοιχα μεγάλη μπαταρία για παιχνίδια, σειρές, social media, ενημέρωση, email, GPS, τα οποία θα χρησιμοποιείς με ιδιαίτερη ευκολία σε αυτό το τηλέφωνο.</p>\n'
