    title: 'Xiaomi Redmi 8 3GB 32GB',
    slug_path: '2155843437/xiaomi-redmi-8-3gb-32gb',
    image_url: 'https://abpcdn.pstatic.gr/P/bpimg45/3x5ZH/1JcqHd_SX585Y1200/0/2155843437.webp',
